.resizable-box {
  width: 100%;
  display: flex;
}
.resizable-box .resiz-bar {
  width: 1%;
  height: inherit;
  background-color: #fff;
  cursor: ew-resize;
  opacity: 0;
  transition: all 0.4s ease-in-out;
}
.resizable-box .resiz-bar:hover {
  opacity: 0.7;
}
.left-box {
  height: 500px;
  background-color: #4169c8;
  overflow: auto;
}
.right-box {
  height: 500px;
  background-color: #783030;
  overflow: auto;
}
