.body {
  width: 100%;
  height: fit-content;
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.body .menu {
  width: fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.body .menu .menu-item {
  width: 300px;
  background-color: brown;
  margin: 5px 0;
  border-radius: 10px;
  transition: 0.5s;
  text-align: start;
  cursor: pointer;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}
.body .menu .menu-item:hover {
  transform: scale(105%, 105%);
}
.body .menu .menu-item .menu-order {
  width: 30px;
  height: 30px;
  margin-left: 5px;
  border: 2px solid black;
  border-radius: 50%;
  text-align: center;
  line-height: 30px;
}
.body .demo-area {
  width: fit-content;
  height: fit-content;
}
.body .demo-area .demo-content {
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.body .demo-area .back-to-menu {
  width: fit-content;
  height: fit-content;
  padding: 5px 0;
  cursor: pointer;
  font-weight: bold;
  text-align: start;
  font-size: 20px;
}
.body .demo-area .back-to-menu:hover {
  color: #2086b6;
}
