* {
  margin: 0;
  padding: 0;
  font-size: 16px;
  box-sizing: border-box;
}
.app-body {
  overflow: hidden;
}
.app-body .app-content {
  overflow: auto;
  width: 100vw;
  min-height: 85vh;
  padding: 40px 0;
  background-color: #a19c9c;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.app-body .app-content .demo-name {
  font-weight: bold;
  font-size: 18px;
}
.app-body .footer {
  width: 100vw;
  height: 15vh;
  background-color: #b3afaf;
  text-align: center;
  line-height: 163px;
  font-weight: bold;
  user-select: none;
}
.app-body .form label {
  display: block;
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 10px;
}
.app-body .form input,
.app-body .form textarea,
.app-body .form select {
  width: 100%;
  height: 4rem;
  padding: 0 10px;
  outline: none;
  border-radius: 5px;
  border: 1px solid #e7e0e0;
  margin-bottom: 10px;
  font-size: 1.5rem;
}
.app-body .form input:focus,
.app-body .form textarea:focus,
.app-body .form select:focus {
  box-shadow: 0 0 2px 2px #c284fc;
}
.app-body .form textarea {
  max-width: 100%;
  min-width: 100%;
  min-height: 8rem;
  padding: 10px 10px;
}
.app-body .form select {
  cursor: pointer;
}
.app-body .form select option {
  display: block;
  width: 100%;
  height: 3rem;
  padding: 10px;
}
.app-body .btn {
  width: fit-content;
  height: fit-content;
  padding: 10px;
  outline: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  display: block;
}
.app-body .btn:disabled {
  cursor: default;
  background-color: #c284fc;
  color: #666666;
}
.app-body .scroll-bar::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 15px;
}
.app-body .scroll-bar::-webkit-scrollbar-track {
  border-radius: 15px;
  background-color: transparent;
}
.app-body .scroll-bar::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  background-color: transparent;
}
