.game-container {
  border: 2px solid black;
  padding: 3px 1px 1px 3px;
  background-color: #9ead86;
}
.game-container .row {
  width: fit-content;
  height: 20px;
  margin-bottom: 2px;
}
.game-container .row .block {
  width: 20px;
  height: 20px;
  border: 2px solid #879372;
  margin-right: 2px;
  padding: 2px;
  float: left;
}
.game-container .row .block::after {
  content: "";
  display: block;
  background-color: #879372;
  width: 12px;
  height: 12px;
}
.game-container .row .active-block {
  border: 2px solid black;
}
.game-container .row .active-block::after {
  background-color: black;
}
.game-container .row .cancelled-block {
  animation: clearBlock 0.5s infinite;
}
.game-container .row .cancelled-block::after {
  background-color: rgba(165, 42, 42, 0.4);
}
@keyframes clearBlock {
  0%,
  40%,
  80% {
    border: 2px solid brown;
  }
  0%::after,
  40%::after,
  80%::after {
    background-color: brown;
  }
  20%,
  60%,
  100% {
    border: 2px solid #879372;
  }
  20%::after,
  60%::after,
  100%::after {
    background-color: #879372;
  }
}
